// componentin perus layout
.container {
    height: 100vh;
    width: 100vw;
    //background-color: #63a4ff;
    //background-image: linear-gradient(315deg, #63a4ff 0%, #83eaf1 74%);
            background: rgb(134,201,210);
            background: radial-gradient(circle, rgba(134,201,210,1) 0%, rgba(0,0,0,1) 100%);


    
    display: flex;
    flex-direction: column;
}

.container .flexRow {
    min-height: 10px;
    
}

// splitattu flexrow
.container .flexRow .split50 {
    display: flex;
    flex-direction: row;
}

.container .flexRow .split50 .flexLeft {
    width: 50%;
    height: 100%;
    text-align: end;
    margin-top: 30px;
    justify-content: end;
    align-items: flex-end;

    animation: slide normal;
    animation-duration: 10s;
    animation-delay: -2s;

      @keyframes slide{
        0%{margin-left:-2500px;}
        20%{margin-left:-2500px;}
        35%{margin-left:0px;}
      100%{margin-left:0px;}
      }
      
    p {
        color: rgb(255, 255, 255);
        padding:55px 67px;
        box-shadow: 0px 0px 9px 3px #6a6a6a50;
        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        font-size: 22px
        
        
    }

    h1 {
        color: white;
        width: 50%;
        box-shadow: 0px 0px 9px 3px #6a6a6a50;
        margin-top: 150px;
        justify-content: end;
        font-family: 'Montserrat', sans-serif;
        font-weight: 400;
        
    }

    
}



.container .flexRow .split50 .flexRight {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    margin-top: 30px;
    margin-bottom: 30px;

animation: slide normal;
    animation-duration: 10s;
    animation-delay: -2s;

      @keyframes slide{
        0%{margin-right:+1500px;}
        20%{margin-right:+1500px;}
        35%{margin-right:0px;}
      100%{margin-right:0px;}
      }

      

    
    img {
        height: 350px;
        margin-top: 12px;
        margin-bottom: 25px;
        box-shadow: 0px 0px 9px 3px #ffd591;
        border-radius: 15px;
        padding: 1px;
        
        
    }

    @media screen and (max-width: 630px) {
        img {
            height: 250px;
        }
    }

    @media screen and (max-width: 460px) {
        img {
            height: 200px;
        }
    }
}


