.container .flexRow .information {

    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
    background: black;
    height: 250px;
    margin-top: 150px;

    background-color: blue;
    color: white;
    font-family: 'Droid Sans', sans-serif;
    text-align: center;

    ul {
        padding: 0;
        font-size: 2em;
    
        li {
          display: inline-block;
          width: 1.2em;
    
          &.grow {
            transition: all .2s ease-in-out;
    
            &:hover {
              transform: scale(1.5);
            }
          }
        }
      }
    
}
